.service_card {
    min-height: 18rem;
    width: 16rem;
    color: white;
    background-color: rgba(0, 0, 0, 0.399);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: baseline;
    border-radius: 19px;
}

.service_card .name{
    font-size: 1.3rem;
    margin-top: 1.5rem;
    /* color: rgb(101, 255, 240); */
    color: rgb(255, 231, 94);
    margin-bottom: 2rem;
}

.service_card .details{
    width: 16rem;
    font-size: 1.1rem;
    text-align: left;
    padding-left: 3rem;
    color: rgb(223, 223, 223);
    line-height: 0.8rem;
}

@media screen and (max-width:960px){
    .service_card {
        min-height: 18rem;
        width: 20rem;
        color: white;
        background-color: rgba(0, 0, 0, 0.399);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: baseline;
        border-radius: 19px;
        margin-bottom: 2rem;
    }
    .service_card .name{
        font-size: 1.4rem;
        margin-top: 1.5rem;
        /* color: rgb(101, 255, 240); */
        color: rgb(255, 231, 94);
        margin-bottom: 2rem;
    }
    .service_card .details{
        width: 18rem;
        font-size: 1.1rem;
        text-align: left;
        padding-left: 3rem;
        color: rgb(223, 223, 223);
        line-height: 0.8rem;
    }
}