.page3{
    background-image: linear-gradient( rgba(28, 28, 28, 0.7), rgba(28, 28, 28, 0.7) ), url('../Assets/390704.jpg');
    background-size: cover;
    max-width: 100vw;
    overflow-x: hidden;
    min-height: 100vh;
    color: white;
}
.content3{
    margin-top: 2rem;
    width: 95%;
    display: flex;
    justify-content: space-evenly;
    align-items: start;
}
.info_details{
    flex: 1.5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.title{
    font-size: 3rem;
    margin-bottom: 1rem; 
}
.allInform{
    width: 95%; 
    padding-top: 0.5rem;
    padding-bottom: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    background-color: rgba(0, 0, 0, 0.527);
    border-radius: 20px;
}
.main_title{
    text-align: left;
    font-size: 1.2rem;
    color: rgb(238, 255, 0);
}

.desc{
    text-align: left;
    font-size: 1rem;
}

.picHolder{
    flex: 1;
}

img.pic2{
    height: 60vh;
    border-radius: 10px;
}

@media screen and (max-width:960px){
    .content3{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .info_details{
        min-width: 100%;
    }
    .title{
        margin-bottom: 1rem;
    }
    img.pic2{
        margin-top: 1rem;
        margin-bottom: 3rem;
        transform: translateY(0.5rem);
        width: 82vw;
        height: auto;
        border-radius: 10px;
    }
    .titles{
        text-align: start;
        text-align: start;
        width: 90vw;
        transform: translateX(0);
    }
    .allInform{
        padding: 1rem;
        font-size: 1.1rem;
        width: 85%;
    }
    .titles span{
        display: none;
    }
}