.page2{
    background-color: rgb(37, 37, 37);
    /* background-image: linear-gradient( rgba(28, 28, 28, 0.7), rgba(28, 28, 28, 0.7) ), url("../Assets/5121.jpg");
    background-size: cover; */
    min-height: 100vh;
    width: 100vw;
    color: rgb(255, 255, 255);
    transition: 0.2s ease;
}

.filler{
    height: 3rem;
}

.title{
    font-size: 4rem;
    font-weight: 400;
    padding-top: 2rem;
}

.skillline{
    width: 7.3rem;
    height: 2px;
    background-color: rgb(133, 0, 51);
    outline: none;
    border: none;
    margin: 4px 0;
    margin-bottom: 3rem;
}

.skills{
    display: flex;
    justify-content: space-evenly;
    margin-top: 3rem;
}

@media screen and (max-width:960px){
    .skills{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        margin-top: 3rem;
    }
    .bar{
        font-size: 1.5rem;
        height: 100%;
        width: 3.8rem;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        transition: 0.1s ease;
    }
    .bar.active{
        background-color: maroon;
        transition: 0.1s ease;
    }
    .alltables{
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        
    }
    .tabl{
        width: 90%;
        margin: 0.5rem 0;
        border-radius: 8px;
        overflow: hidden;
        transform: translateY(-3vh);
    }
    tr{
        height: 3rem;
    }
    th,td{
        height: 1rem;
        width: 50%;
    }

}