.header {
  width: 100vw;
  height: 3.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.541);
  position: fixed;
  top: 0;
  z-index: 999;
  /*animation: headerpop 1s forwards ease;*/
}

@keyframes headerpop {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.filler {
  height: 3.5rem;
  background-color: rgb(38, 38, 38);
}

.logo {
  text-decoration: none;
  font-size: 1.6rem;
  margin-left: 5vw;
  color: rgb(255, 255, 255);
  cursor: pointer;
}

#itratee {
  animation: itrr 3.5s infinite ease-in-out;
  background: -webkit-linear-gradient(
    right,
    rgb(47, 255, 47),
    rgb(69, 153, 250),
    rgb(68, 0, 255)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@keyframes itrr {
  0% {
    transform: rotate(0);
  }
  28% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(180deg);
  }
  72% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.menuitems_desktop {
  margin-right: 1vw;
  width: 40%;
  display: flex;
  justify-content: space-evenly;
}

.menuitems {
  display: none;
}

.bar {
  display: none;
}

.btnn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: rgb(255, 255, 255);
  outline: none;
  border-radius: 5px;
  border: none;
  height: 2.3rem;
  width: 5rem;
  background: transparent;
  font-weight: bold;
  font-size: 1rem;
  border: 2px solid rgba(255, 102, 0, 0);
  cursor: pointer;
}

.btnn:hover {
  border: 2px solid rgb(238, 255, 0);
}
.btnn:active {
  border: 2px solid rgb(238, 255, 0);
  background-color: rgb(255, 143, 68);
}

@media screen and (max-width: 960px) {
  .header {
    position: fixed;
    top: 0;
    width: 100vw;
  }

  .menuitems_desktop {
    display: none;
  }

  .menuitems {
    position: fixed;
    display: flex;
    flex-direction: column;
    background-color: rgb(27, 27, 27);
    top: 3.5rem;
    left: 100vw;
    transform: translateX(0);
    width: 12rem;
    border-bottom-left-radius: 10px;
    transition: 0.2s ease;
    z-index: 999;
  }
  .rotateicon {
    color: rgb(255, 255, 255);
    transform: rotate(0deg);
    transition: all 0.3s ease;
  }
  .rotateicon.active {
    color: white;
    transform: rotate(90deg);
    transition: all 0.3s ease;
  }

  .menuitems.active {
    transform: translateX(-100%);
    transition: 0.2s ease;
  }
  .menuitems .btnn {
    height: 4rem;
    width: 100%;
    outline: none;
    border-radius: 0;
    font-size: 1.3rem;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  .menuitems .btnn:hover {
    outline: none;
    border: none;
  }
  .menuitems .btnn:active {
    outline: none;
    border: none;
  }
  .bar {
    font-size: 1.5rem;
    height: 100%;
    width: 3.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 255, 255);
    transition: 0.1s ease;
  }
  .bar .active {
    transition: 0.1s ease;
  }
}
