.page4{
    background-image: linear-gradient( rgba(28, 28, 28, 0.7), rgba(28, 28, 28, 0.7) ), url('../Assets/5121.jpg');
    background-size: cover;
    width: 100vw;
    height: 100vh;
}
.contentContact{
    display: flex;
    width: 100vw;
    justify-content: space-between;
    align-items: center;
    min-height: 100vh;
}
.allinform3{
    padding-left: 5rem;
    text-align: start;
    text-align: justify;
    width: 30vw;
    color: white;
    font-size: 2rem;
    font-weight: 500;
}
.downloadbtn{
    padding: 1rem;
    border: none;
    outline: none;
    font-size: 1.1rem;
    background-color: rgb(238, 255, 0);
    border-radius: 5px;
    color: rgb(0, 0, 0);
    font-weight: bold;
    cursor: pointer;
}
.downloadbtn:hover{
    background-color: rgb(151, 161, 12);
}
.downloadbtn:active{
    background-color: rgb(166, 105, 0)
}
.forma2{
    width: 50vw;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:960px){
    .contentContact{
        display: flex;
        flex-direction: column;
        width: 100vw;
        justify-content: space-evenly;
        align-items: center;
        height: 80vh;
    }
    .allinform3{
        padding: 0;
        text-align: start;
        text-align: left;
        width: 90vw;
        transform: translateX(0);
        color: white;
        font-size: 2rem;
        font-weight: 400;
    }
    .forma2{
        width: 90vw;
        height: fit-content;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}