.page1{
    background-image: linear-gradient( rgba(28, 28, 28, 0.7), rgba(28, 28, 28, 0.7) ), url('../Assets/390704.jpg');
    background-size: cover;
    width: 100vw;
    overflow-x: hidden;
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    color: white;
}

.content1{
    padding-bottom: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    visibility: visible;
    animation: conten 0.5s forwards 0.4s ease;
}

img.pic1{
    width: 40vw;
    transform: translateX(-10vw);
    /*visibility: hidden;*/
    visibility: visible;
    border-radius: 10px;
    animation: imagee 0.5s forwards 0.8s ease;
}

.intro{
    font-size: 1.4rem;
    width: 70vw;
    line-height: 2rem;
    font-style: italic;
}

#resume{
    color: rgb(255, 255, 255);
    background-color: rgb(238, 255, 0);
    border: 1.5px solid rgb(238, 255, 0);
    color: rgb(54, 33, 0);
    outline: none;
    font-size: 1.3rem;
    height: 2.7rem;
    padding-left: 1rem;
    padding-right: 1rem;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 1rem;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

#resume:hover{
    border: 2px solid rgb(0, 0, 0);
}
#resume:active{
    background-color: rgb(248, 255, 144);
}

#animm{
    animation: gradiental 1s infinite linear 0.3s;
}

a{
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@keyframes gradiental{
    from{
        background: -webkit-linear-gradient(right,  rgb(238, 255, 1), rgb(0, 238, 255));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    to{
        background: -webkit-linear-gradient(right,  rgb(238, 255, 1), rgb(0, 238, 255));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}

@media screen and (max-width:960px){

    .page1{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        height: 100vh;
    }
    .container{
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }

    img.pic1{
        width: 90vw;    
        transform: translateX(0);
    }
    .cotent1{
        transform: translateX(0);
        margin-left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}